import dateRangePicker from "../dateRangePicker.vue";
import tabStyles from "../../../../components/tabsView";
import moment from "moment";

import { APIServices } from "../../../../api/api-services";

export default {
    components: {
        "date-range-picker": dateRangePicker
    },
    props: {
        pageTitle: {
            type: String
        }
    },
    data() {
        return {
