                    }
                ]
            },
            workFilter: {
                open: false,
                active: true,
                history: false
            },
            jobs: {
                open: [
                    //STATUS: 0 - Unscheduled, 1 - Unassigned, 2 - Scheduled, 3 - In Progress, 4 - Completed, 5 - Canceled
                    {
                        id: 1,
                        title: "Job Name Title",
                        price: 50,
                        status: 0,
                        isVIP: false,
                        showDetails: false,
                        isAutobooked: true
                    },
                    {
                        id: 2,
                        title: "Job Name Title",
                        price: 100,
