    },
    filters: {
        mappedStatus: function(val) {
            switch (val) {
                case 0:
                    return "Open";
                    break;
                case 1:
                    return "Open";
                    break;
                case 2:
                    return "Scheduled";
                    break;
                case 3:
                    return "In Progress";
                    break;
                case 4:
                    return "Completed";
                    break;
                case 5:
                    return "Canceled";
                    break;
            }
